.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}

.move-enter-active {
  opacity: 1;
  transition: all 1s linear;
}

.move-exit-active {
  opacity: 0;
  transition: all 1s linear;
}
